.table {
    background-color: white;
    box-shadow: 0 1px 2px -2px #00000029, 0 3px 6px 0 #0000001f, 0 5px 12px 4px #00000017;
    border: 1px solid #d3d3d3;
    padding: 5px;
    border-radius: 5px;
    th {
        background-color: #d3d3d3;
        padding: 10px;
        border: 1px solid #d3d3d3;
    }
}